.gpt3__header {
  display: flex;
  flex-direction: row;
}

.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}

.gpt3__header-content h1 {
  font-weight: 800;
  font-size: 52px;
  line-height: 38px;
  color: #ffffff;
  letter-spacing: -0.04rem;
  line-height: 75px;
}

.gpt3__header-content p {
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #81afdd;
  margin-top: 1.5rem;
}

.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.gpt3__header-content__input{
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.gpt3__header-content__input input {
  background: #052d56;
  border-radius: 5px 0px 0px 5px;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #3d6184;
  flex: 2;
  width: 100%;
  min-height: 50px;
  border: 2px solid var(--color-footer);
  padding: 0 1rem;
  outline: none;
}

.gpt3__header-content__input button {
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  color: #ffffff;
  background: #ff4820;
  border-radius: 5px 0px 0px 5px;
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border-radius: 0 5px 5px 0;
  border: none;
}

.gpt3__header-content__input button:hover{
  opacity: .7;
}

.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}

.gpt3__header-content__people p {
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #ffffff;
  text-align: center;
  margin: 0 0 0 1rem;
}

.gpt3__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1020px) {
    .gpt3__header{
        flex-direction: column;
    }

    .gpt3__header-content{
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }

    .gpt3__header-content__people{
        flex-direction: column;
    }

    .gpt3__header-content__people p{
        margin: 0;
    }

    .gpt3__header-content__input,
    .gpt3__header-content__button{
        line-height: 24px;
        font-size: 16px;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header p{
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content__people{
        flex-direction: column;
    }

    .gpt3__header-content__input,
    .gpt3__header-content__button{
        line-height: 12px;
        font-size: 16px;
    }
}