.gpt3__possibility {
  display: flex;
  flex-direction: row;
}

.gpt3__possibility-image {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 2rem;
}

.gpt3__possibility-image img {
  width: 100%;
  height: 100%;
}

.gpt3__possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}

.gpt3__possibility-content h4 {
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71e5ff;
}

.gpt3__possibility-content h4:last-child {
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #ff8a71;
  cursor: pointer;
}

.gpt3__possibility-content h1 {
  font-weight: 800;
  font-size: 31px;
  line-height: 45px;
  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%), #ffffff;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 1rem 0;
}

.gpt3__possibility-content p {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: #81afdd;
}

@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
    }

    .gpt3__possibility-image{
        margin: 1rem 0;
    }

    .gpt3__possibility-content{
        margin-top: 2rem;
    }
}